import request from "@/utils/request";

export function login(data) {
  return request({
    url: "/user/login",
    method: "post",
    data
  });
}

export function getInfo(token) {
  return request({
    url: "/user/info",
    method: "get",
    params: { token }
  });
}

export function logout(token) {
  return request({
    url: "/user/logout",
    method: "post",
    params: { token }
  });
}

export function getUserList(token) {
  return request({
    url: "/user/list",
    method: "get",
    params: { token }
  });
}

export function changePass(token, data) {
  return request({
    url: "/user/repass",
    method: "post",
    params: { token },
    data
  });
}
