import Vue from "vue";
import Router from "vue-router";
import Layout from "../../layout/index.vue";
// import store from "../store/index";

Vue.use(Router);

const constantRoutes = [
  {
    path: "/",
    component: Layout,
    children: [
      {
        path: "/",
        name: "Home",
        component: () => import("../views/Home/index"),
        meta: {
          title: "首页"
        }
      }
    ]
  },
  {
    path: "/plan",
    component: Layout,
    children: [
      {
        path: "/plan",
        name: "Plan",
        component: () => import("../views/Plan/index"),
        meta: {
          title: "计划"
        }
      }
    ]
  },
  {
    path: "/overdue",
    component: Layout,
    children: [
      {
        path: "/overdue",
        name: "Overdue",
        component: () => import("../views/Overdue/index"),
        meta: {
          title: "逾期"
        }
      }
    ]
  },
  {
    path: "/completed",
    component: Layout,
    children: [
      {
        path: "/completed",
        name: "Completed",
        component: () => import("../views/Completed/index"),
        meta: {
          title: "已完成"
        }
      }
    ]
  },
  {
    path: "/statistics",
    component: Layout,
    children: [
      {
        path: "/statistics",
        name: "Statistics",
        component: () => import("../views/Statistics/index"),
        meta: {
          title: "统计"
        }
      }
    ]
  },
  {
    path: "/settings",
    component: Layout,
    children: [
      {
        path: "/settings",
        name: "Settings",
        component: () => import("../views/Settings/index"),
        meta: {
          title: "设置"
        }
      }
    ]
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login/index"),
    meta: {
      title: "登陆",
      first: true
    }
  },
  // {
  //   path: "/register",
  //   name: "Register",
  //   component: () => import("../views/Register/index"),
  //   meta: {
  //     title: "注册",
  //     first: true
  //   }
  // },
  {
    path: "/404",
    component: () => import("../views/NotFoundPage/index")
  },
  {
    path: "*",
    redirect: "/404",
    hidden: true
  }
];

const createRouter = () =>
  new Router({
    // mode: 'history', // require service support
    scrollBehavior: () => ({ y: 0 }),
    routes: constantRoutes
  });

const router = createRouter();

export function resetRouter() {
  const newRouter = createRouter();
  router.matcher = newRouter.matcher; // reset router
}

export default router;
